    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

a {
  text-decoration: none;
  color: white; }
  a:hover {
    color: rgba(255, 255, 255, 0.85); }

body {
  position: relative;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url(/movie-website/static/media/bg.a913cb74.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-color: #191b1e;
  color: white; }

.logo {
  font-family: 'Anton', sans-serif; }

.form-control {
  color: rgba(255, 255, 255, 0.8); }
  .form-control:focus {
    color: rgba(255, 255, 255, 0.8);
    border-color: #0d6efd;
    box-shadow: none; }

.btn-outline-primary:focus {
  box-shadow: none !important; }

.bg-dark {
  background: linear-gradient(rgba(37, 37, 37, 0.8), rgba(37, 37, 37, 0.8)), url(/movie-website/static/media/bg.a913cb74.jpg);
  background-size: cover;
