        mask-image: linear-gradient(to top, transparent 0%, black 100%); }
      .carousel .carousel-inner .carousel-item .carousel-movie-info {
        z-index: 1;
        position: absolute;
        left: 50%;
        -webkit-transform: translate(-50%);
                transform: translate(-50%);
        text-align: center;
        padding: 0 15px;
        width: 80%;
        display: block; }
        .carousel .carousel-inner .carousel-item .carousel-movie-info h1 {
          -webkit-text-stroke: 1px white;
          color: transparent; }
        .carousel .carousel-inner .carousel-item .carousel-movie-info .btn {
