.row {
  z-index: 1; }

.slider-wrap {
  z-index: 1;
  margin-top: 15px;
  position: relative; }
  .slider-wrap .slider {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    overflow: auto;
    margin-bottom: 20px;
    padding: 0;
    z-index: 1; }
    .slider-wrap .slider .item {
      margin: 10px 20px 10px 0;
      transition: all 0.4s ease-in-out; }
      .slider-wrap .slider .item img {
        height: 300px;
        width: 200px; }
      .slider-wrap .slider .item:hover {
        -webkit-transform: scale(1.05);
                transform: scale(1.05); }
    .slider-wrap .slider .left-arrow, .slider-wrap .slider .right-arrow {
      position: absolute;
      top: 50%;
      color: rgba(255, 255, 255, 0.83);
      font-size: 30px;
      font-weight: 100;
      cursor: pointer;
      transition: all 0.4s ease-in-out; }
      .slider-wrap .slider .left-arrow:hover, .slider-wrap .slider .right-arrow:hover {
        -webkit-transform: scale(1.5);
                transform: scale(1.5); }
    .slider-wrap .slider .left-arrow {
      left: -30px; }
    .slider-wrap .slider .right-arrow {
      right: -30px; }
    .slider-wrap .slider::-webkit-scrollbar {
      display: none; }

.movie-grid {
  margin: 100px 0 20px 0; }
  .movie-grid .title {
    margin-bottom: 30px; }
  .movie-grid .movie-card {
    background-color: transparent;
    border: none;
    margin: 10px 0; }
    .movie-grid .movie-card .card-img-top:hover {
      -webkit-transform: scale(1.05);
              transform: scale(1.05); }
    .movie-grid .movie-card .card-img-top img {
      height: 100%;
      width: 100%;
      object-fit: cover; }

.card-img-top {
  border-radius: 0px;
  transition: all 0.4s ease-in-out;
  height: 100%; }

.no-img {
  height: 100%;
  background-color: rgba(255, 255, 255, 0.714);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  color: #1d1f229e;
  text-align: center; }

.show-more {
  margin: 20px 10px; }

.disable {
  pointer-events: none; }
  .disable:hover {
    color: white; }

@media (min-width: 992px) {
  .left-arrow, .right-arrow {
    display: visible; }
  .carousel {
    height: 500px; }
    .carousel .carousel-inner .carousel-movie-info {
      bottom: 46% !important;
      width: 50% !important; } }

@media (max-width: 992px) {
  .carousel-movie-info {
    bottom: 30% !important; }
  .left-arrow, .right-arrow {
    display: none; }
  .container {
    max-width: 100%; } }

@media (max-width: 548px) {
