    padding-top: 200px; }
    .movie-image-info .image-lg-dev {
      display: none; }
    .movie-image-info .image-sm-dev {
      display: inline-block; }
    .movie-image-info .tickets-btn {
      margin: 10px 0px;
      width: 100%; } }

